<template>
  <div id="portfolio" class="portfolio">
    <Header />
    <div class="portfolio-center" v-if="portfolioData != null">
      <div class="portfolio-title">{{ portfolioData.Name }}</div>
      <vueper-slides bullets-outside :dragging-distance="50" :slide-ratio="9 / 16">
        <vueper-slide
          v-for="(slide, i) in slides"
          :key="i"
          :image="slide.image"
          :video="slide.isvideo ? slide.video : undefined"
        />
      </vueper-slides>
      <div class="portfolio-text-area">
        <label class="portfolio-tag" v-for="t in portfolioData.Tag" :key="t">{{ `#${t}` }}</label>
        <div class="portfolio-info-area" v-for="(desc, idx) in portfolioData.Desc" :key="idx">
          <div class="info-title" v-html="desc.Title"></div>
          <div class="info-content" v-html="desc.Content"></div>
        </div>
        <a v-if="showDemoUrl(portfolioData.Url)" class="portfolio-demo" :href="portfolioData.Url" target="blank">
          ↪ 網站Demo ↩
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '@/components/header/Header.vue';
import portfolioData from '@/json/portfolioData.json';
import { VueperSlides, VueperSlide } from 'vueperslides';
import 'vueperslides/dist/vueperslides.css';

export default {
  mounted() {
    this.serial = this.GetParameterByName('s');
    this.getPortfolioData();
    this.intiSlides();
  },
  data() {
    return {
      portfolioList: portfolioData.Info,
      portfolioTags: portfolioData.Tags,
      serial: null,
      portfolioData: null,
      slides: [],
    };
  },
  computed: {
    portfolioImage: function() {
      return this.portfolioData != null ? this.portfolioData.Img : [];
    },
    portfolioVideo: function() {
      return this.portfolioData != null ? this.portfolioData.VideoUrl : [];
    },
  },
  methods: {
    getPortfolioData() {
      let idx = this.portfolioList.findIndex(obj => obj.Serial == this.serial);
      console.log('idx:' + idx);
      if (idx < 0) {
        idx = 0;
        this.serial = this.portfolioList[idx].Serial;
      }
      this.portfolioData = this.portfolioList[idx];
    },
    showDemoUrl(url) {
      return url == '' ? false : true;
    },
    intiSlides() {
      for (let i = 0; i < this.portfolioImage.length; i++) {
        let tmpdata = {
          image: require('@/assets/img/portfolio/' + this.portfolioImage[i]),
          video: {
            url: '',
          },
          isvideo: false,
        };
        this.slides.push(tmpdata);
      }
      for (let i = 0; i < this.portfolioVideo.length; i++) {
        let tmpdata = {
          image: '',
          video: {
            url: this.portfolioVideo[i] + '?enablejsapi=1', //?controls=0&fs=0&modestbranding=1&color=white&iv_load_policy=3&autohide=1&enablejsapi=1
            props: {
              allow: '',
              allowfullscreen: 'allowfullscreen',
            },
          },
          isvideo: true,
          pointerEvents: false,
        };
        this.slides.push(tmpdata);
      }
    },
  },
  components: { Header, VueperSlides, VueperSlide },
};
</script>
<style>
.vueperslides__arrow,
.vueperslides__arrow svg {
  background: black;
  border-radius: 80px;
}

.vueperslides__arrow svg {
  width: 1.4em;
  padding: 0.4em;
}

.vueperslides__arrow:hover {
  opacity: 0.7;
}
</style>
